<template>
  <div class="row">
    <form @submit.prevent="onSubmit" class="col l6 m8 s12 offset-l3 offset-m2 offset-s0">
      <div class="row">
        <h5>3. Переработки и праздничные дни</h5>
      </div>
      <div class="data-list">
        <label>
          <input type="checkbox" class="filled-in" v-model="overtimeChecked" />
          <span>Были переработки</span>
        </label>
        <div class="inputs-list" v-show="overtimeChecked">
          <InputWithButtons
            id="overtime-shifts"
            placeholder="Количество смен"
            v-model="overtime.shifts"
            :required="true"
          />
          <InputWithButtons
            id="overtime-nights"
            placeholder="Из них ночных"
            v-model="overtime.nights"
            :required="true"
          />
        </div>

        <label>
          <input type="checkbox" class="filled-in" v-model="holidayChecked" />
          <span>Работал в праздники</span>
        </label>
        <div class="inputs-list" v-show="holidayChecked">
          <InputWithButtons
            id="holidays"
            placeholder="Часов"
            v-model="holidays"
            :required="true"
          />
        </div>
      </div>

      <ControlButtons linkBack="/shifts" nextText="Следующий шаг" />
    </form>
  </div>
</template>

<script>
import ControlButtons from "@/components/ControlButtons";
import InputWithButtons from "@/components/InputWithButtons";
export default {
  name: "overtime",
  components: { ControlButtons, InputWithButtons },
  data: () => ({
    overtime: {
      shifts: 0,
      nights: 0
    },
    holidays: 0,
    overtimeChecked: false,
    holidayChecked: false
  }),
  mounted() {
    M.updateTextFields();
  },
  methods: {
    changeOvertime(type) {
      if (type === "down") this.overtime--;
      else this.overtime++;
    },
    changeHoliday(type) {
      if (type === "down") this.holidays--;
      else this.holidays++;
    },
    onSubmit() {
      let overtime = this.overtimeChecked ? this.overtime : 0;
      let holidays = this.holidayChecked ? this.holidays : 0;
      let overtimes = {
        overtime,
        holidays
      };
      this.$store.dispatch("setOvertimes", overtimes);
      this.$router.push("/other");
    }
  }
};
</script>

<style lang='scss' scoped>
label {
  display: block;
  margin-bottom: 1rem;
}
.btn-plus {
  float: right;
}
.btn-count {
  margin-top: 1rem;
}
</style>